.doctors {

    &__order {
        $image-width: 445px;
        $image-width-xl: 350px;
        $image-width-lg: 275px;
        $image-width-md: 400px;

        display: flex;
        flex-wrap: wrap;
        width: 100%;
        @include media(md) {
            margin: 0 0 20px 0;
        }

        &-image {
            width: $image-width;
            background-color: rgba(66, 110, 170, 0.1);
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-end;
            max-height: 597px;
            @include media(xl) {
                width: $image-width-xl;
                max-height: 473px;
            }
            @include media(lg) {
                width: $image-width-lg;
                max-height: 380px;
            }
            @include media(md) {
                width: $image-width-md;
                max-height: none;
            }
            @include media(sm) {
                width: 100%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }

        &-form {
            width: calc(100% - #{$image-width} - 40px);
            border: 1px solid #E3EFFF;
            border-radius: 10px;
            padding: 30px;
            max-width: 500px;
            margin: 0 0 0 40px;
            @include media(xl) {
                width: calc(100% - #{$image-width-xl} - 25px);
                margin: 0 0 0 25px;
                padding: 20px;
            }
            @include media(lg) {
                width: calc(100% - #{$image-width-lg} - 15px);
                margin: 0 0 0 15px;
                padding: 15px;
            }
            @include media(md) {
                width: calc(100% - #{$image-width-md} - 15px);
                max-width: unset;
            }
            @include media(sm) {
                display: none;
                width: 100%;
                max-width: 600px;
                margin: unset;
                border: unset;
                padding: 30px;
            }

            &-title {
                font-size: 32px;
                margin: 0 0 25px 0;
                @include media(xl) {
                    font-size: 24px;
                }
                @include media(lg) {
                    font-size: 18px;
                    margin: 0 0 15px 0;
                }
            }

            .doctors__grid {
                &-order {
                    &-header {
                        @include media(lg) {
                            flex-direction: column;
                            margin: 0 0 15px 0;
                        }
                        @include media(md) {
                            flex-direction: row;
                        }
                    }

                    &-time {
                        > li {
                            > a {
                                @include media(lg) {
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                }

            }

        }

    }

}
