.pagination {
  padding: 30px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  @include media(xl) {
    padding: 15px 0;
  }

  &.align {
    &-left {
      justify-content: flex-start;
    }
  }

  &__loadmore {
    border-color: $color-blue;
    color: $color-blue;
  }
}
